'use client';

import React, { useState, useEffect } from 'react';
import {
  Select,
  SelectContent,
  SelectItem,
  SelectTrigger,
  SelectValue,
} from '@/components/ui/select';
import {
  Card,
  CardContent,
} from '@/components/ui/card';
import {
  Bar,
  BarChart,
  CartesianGrid,
  ResponsiveContainer,
  Tooltip,
  XAxis,
  YAxis,
  Legend,
} from 'recharts';
import contributorData from '../../../contributor-repo-timeline.json';

// Repository list with colors and mappings
const repositories = [
  { name: 'Prebid.js', key: 'prebid-js', color: '#10B981' },      // Green
  { name: 'Prebid Server', key: 'prebid-server', color: '#8B5CF6' },   // Purple
  { name: 'Prebid Server Java', key: 'prebid-server-java', color: '#EF4444' }, // Red
  { name: 'Prebid Mobile iOS', key: 'prebid-mobile-ios', color: '#F59E0B' },  // Gold
  { name: 'Prebid Mobile Android', key: 'prebid-mobile-android', color: '#3B82F6' }, // Blue
  { name: 'Prebid Docs', key: 'prebid-github-io', color: '#14B8A6' }      // Teal
];

export default function ContributorsPage() {
  const [timeRange, setTimeRange] = useState('This Year');
  const [metric, setMetric] = useState('Merged PRs');
  const [selectedRepo, setSelectedRepo] = useState('All');
  const [chartData, setChartData] = useState<any[]>([]);

  // Process data based on selections
  useEffect(() => {
    const processData = () => {
      const contributorMap = new Map<string, Record<string, number>>();
      const currentDate = new Date();
      const currentYear = currentDate.getFullYear();
      const currentWeek = getWeekNumber(currentDate);
      
      // Determine time key based on selection
      let timeKey = 'y'; // year
      let targetPeriod = '';
      
      if (timeRange === 'This Week') {
        timeKey = 'w';
        targetPeriod = `${currentYear}-${String(currentWeek).padStart(2, '0')}`;
      } else if (timeRange === 'Last Week') {
        timeKey = 'w';
        targetPeriod = `${currentYear}-${String(currentWeek - 1).padStart(2, '0')}`;
      } else if (timeRange === 'This Month') {
        timeKey = 'm';
        targetPeriod = `${String(currentYear).slice(-2)}-${String(currentDate.getMonth() + 1).padStart(2, '0')}`;
      } else if (timeRange === 'Last Month') {
        timeKey = 'm';
        const lastMonth = currentDate.getMonth() === 0 ? 12 : currentDate.getMonth();
        const year = currentDate.getMonth() === 0 ? currentYear - 1 : currentYear;
        targetPeriod = `${String(year).slice(-2)}-${String(lastMonth).padStart(2, '0')}`;
      } else if (timeRange === 'This Year') {
        timeKey = 'y';
        targetPeriod = String(currentYear).slice(-2);
      } else if (timeRange === 'Last Year') {
        timeKey = 'y';
        targetPeriod = String(currentYear - 1).slice(-2);
      }
      
      // Determine metric index: [openedPRs, mergedPRs, openedIssues]
      let metricIndex = 1; // Default to merged PRs
      if (metric === 'Opened PRs') metricIndex = 0;
      else if (metric === 'Merged PRs') metricIndex = 1;
      else if (metric === 'Opened Issues') metricIndex = 2;
      
      // Process contributor data
      Object.entries(contributorData.data).forEach(([contributor, repos]: [string, any]) => {
        const repoData: Record<string, number> = {
          'Prebid.js': 0,
          'Prebid Server': 0,
          'Prebid Server Java': 0,
          'Prebid Mobile iOS': 0,
          'Prebid Mobile Android': 0,
          'Prebid Docs': 0
        };
        
        // Process each repository
        repositories.forEach(repo => {
          if (selectedRepo === 'All' || selectedRepo === repo.name) {
            const repoKey = repo.key;
            if (repos[repoKey] && repos[repoKey][timeKey] && repos[repoKey][timeKey][targetPeriod]) {
              const value = repos[repoKey][timeKey][targetPeriod][metricIndex] || 0;
              repoData[repo.name] = value;
            }
          }
        });
        
        // Only add contributor if they have any contributions
        const total = Object.values(repoData).reduce((sum, val) => sum + val, 0);
        if (total > 0) {
          contributorMap.set(contributor, repoData);
        }
      });
      
      // Convert to array and sort by total contributions
      const dataArray = Array.from(contributorMap.entries())
        .map(([name, repos]) => ({
          name,
          ...repos,
          total: Object.values(repos).reduce((sum, val) => sum + val, 0)
        }))
        .sort((a, b) => b.total - a.total)
        .slice(0, 20) // Top 20 contributors
        .map(({ total, ...rest }) => rest);
      
      setChartData(dataArray);
    };
    
    processData();
  }, [timeRange, metric, selectedRepo]);
  
  // Helper function to get week number
  function getWeekNumber(date: Date): number {
    const firstDayOfYear = new Date(date.getFullYear(), 0, 1);
    const pastDaysOfYear = (date.getTime() - firstDayOfYear.getTime()) / 86400000;
    return Math.ceil((pastDaysOfYear + firstDayOfYear.getDay() + 1) / 7);
  }

  return (
    <div className="w-full py-8" style={{ paddingLeft: '0.1rem', paddingRight: '0.1rem', paddingTop: '2rem' }}>
      {/* Selectors */}
      <div className="flex" style={{ paddingLeft: '1rem', paddingRight: '1rem', marginBottom: '2rem', gap: '2rem' }}>
        {/* Repository Selector */}
        <Select value={selectedRepo} onValueChange={setSelectedRepo}>
          <SelectTrigger className="w-[200px]">
            <SelectValue placeholder="Select repository" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="All">All</SelectItem>
            {repositories.map((repo) => (
              <SelectItem key={repo.name} value={repo.name}>{repo.name}</SelectItem>
            ))}
          </SelectContent>
        </Select>

        {/* Metric Selector */}
        <Select value={metric} onValueChange={setMetric}>
          <SelectTrigger className="w-[200px]">
            <SelectValue placeholder="Select metric" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="Merged PRs">Merged PRs</SelectItem>
            <SelectItem value="Reviewed PRs">Reviewed PRs</SelectItem>
            <SelectItem value="Merged Commits">Merged Commits</SelectItem>
            <SelectItem value="Opened Issues">Opened Issues</SelectItem>
            <SelectItem value="Opened PRs">Opened PRs</SelectItem>
          </SelectContent>
        </Select>
        
        {/* Time Range Selector */}
        <Select value={timeRange} onValueChange={setTimeRange}>
          <SelectTrigger className="w-[200px]">
            <SelectValue placeholder="Select time range" />
          </SelectTrigger>
          <SelectContent>
            <SelectItem value="This Week">This Week</SelectItem>
            <SelectItem value="Last Week">Last Week</SelectItem>
            <SelectItem value="This Month">This Month</SelectItem>
            <SelectItem value="Last Month">Last Month</SelectItem>
            <SelectItem value="This Year">This Year</SelectItem>
            <SelectItem value="Last Year">Last Year</SelectItem>
          </SelectContent>
        </Select>
      </div>

      {/* Stacked Horizontal Bar Chart */}
      <div style={{ paddingLeft: '1rem', paddingRight: '1rem' }}>
        <Card>
          <CardContent className="pt-6">
            <div className="h-[600px]">
              <ResponsiveContainer width="100%" height="100%">
                <BarChart 
                  data={contributorData} 
                  layout="horizontal"
                  margin={{ top: 20, right: 30, bottom: 40, left: 100 }}
                >
                  <CartesianGrid 
                    strokeDasharray="3 3" 
                    horizontal={false}
                    vertical={true}
                    strokeOpacity={0.3}
                  />
                  <XAxis 
                    type="number"
                    tickLine={false}
                    axisLine={false}
                    tick={{ fontSize: 11 }}
                    stroke="#888888"
                  />
                  <YAxis 
                    type="category"
                    dataKey="name"
                    tickLine={false}
                    axisLine={false}
                    tick={{ fontSize: 11 }}
                    stroke="#888888"
                  />
                  <Tooltip 
                    contentStyle={{
                      backgroundColor: 'hsl(var(--background))',
                      border: '1px solid hsl(var(--border))',
                      borderRadius: 'var(--radius)',
                    }}
                  />
                  <Legend 
                    layout="vertical"
                    align="right"
                    verticalAlign="middle"
                    wrapperStyle={{ 
                      paddingLeft: '20px',
                    }}
                    content={(props) => {
                      const { payload } = props;
                      return (
                        <ul style={{ listStyle: 'none', padding: 0, margin: 0 }}>
                          {payload?.map((entry, index) => (
                            <li key={`item-${index}`} style={{ display: 'flex', alignItems: 'center', marginBottom: '24px' }}>
                              <span
                                style={{
                                  display: 'inline-block',
                                  width: '20px',
                                  height: '20px',
                                  backgroundColor: entry.color,
                                  marginRight: '12px',
                                }}
                              />
                              <span style={{ fontSize: '16px' }}>{entry.value}</span>
                            </li>
                          ))}
                        </ul>
                      );
                    }}
                  />
                  {repositories.map((repo) => (
                    <Bar 
                      key={repo.name}
                      dataKey={repo.name}
                      stackId="a"
                      fill={repo.color}
                    />
                  ))}
                </BarChart>
              </ResponsiveContainer>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
}